    p Bienvenido estimado Aprendiz:
      br
      br
      | El programa Tecnología en Supervisión en Procesos de Confección, se creó para brindar al sector productivo de confección, la posibilidad de incorporar personal con altas calidades laborales y profesionales que contribuyan al desarrollo económico, social y tecnológico de su entorno y del país. También busca ofrecer a la población formación en las tecnologías de la producción de confección, para aportar a la productividad, fortalecimiento y crecimiento socioeconómico de la región y la nación.
      br
      br
      | La evolución y el fortalecimiento de los sectores productivos dependen en gran medida de un recurso humano cualificado y calificado, en capacidad de responder integralmente a la dinámica de cada sector, con ambientes adecuados en lo referente a la infraestructura física, el montaje de equipos y al sistema moderno de producción en celdas trabajo; lo que además, implica el conocimiento de los conceptos de productividad, que lo acreditan y lo hacen pertinente y coherente con su misión, innovando permanentemente de acuerdo con las tendencias y cambios tecnológicos y las necesidades del sector empresarial y de los trabajadores, lo que permite generar un impacto positivo en la productividad, la competitividad, la equidad y el desarrollo del país.
      br
      br
      | El SENA, da respuesta a las necesidades de este sector por medio de esta oferta educativa que le permitirá ofrecer escenarios para el desarrollo de mejores prácticas y habilidades en el análisis y gestión de los procesos, con el propósito de establecer escenarios de optimización en la productividad de las diferentes áreas de la organización y consolidar el sector ante los nuevos retos de los mercados, según acuerdos comerciales.
      br
      br
