rios para el desarrollo de mejores prácticas y habilidades en el análisis y gestión de los procesos, con el propósito de establecer escenarios de optimización en la productividad de las diferentes áreas de la organización y consolidar el sector ante los nuevos retos de los mercados, según acuerdos comerciales."),_c('br'),_c('br'),_vm._v("Le invitamos a abrirse camino aprovechando esta gran oportunidad de formación.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro p-4 p-md-5 mb-5"},[_c('div',{staticClass:"titulo__template--a mb-4"},[_c('span',{staticClass:"h4"},[_vm._v("2. Justificación del programa")])]),_c('p',{staticClass:"mb-4"},[_vm._v("En Colombia el sector Textil-Confección es uno de los sectores más importantes de la industria, generando 7,5 % de la producción de prendas de vestir, según el clúster textil confección, diseño y moda, mientras que el resto del sector industrial lo ha hecho solo en el 0.7%, sin embargo la industria textil, es uno de los sectores más destacables de la economía colombiana, debido a que equivale al 10 % del PIB y representa aproximadamente el 24% del empleo en el país de acuerdo a Cámara Colombiana de la confección, evidenciando ser un sector dinámico y con gran capacidad de respuesta para satisfacer tanto la demanda interna como externa."),_c('br'),_c('br'),_vm._v("Por su parte, el Gobierno Nacional muestra interés en fomentar la competitividad de la industria textil colombiana, mediante nuevas líneas de crédito a través del Ministerio de Comercio, Industria y Turismo. Además, también se han identificado las brechas tecnológicas que debe superar la industria. Por ello, se está diseñando un programa de manufactura inteligente que consiste en un conjunto de tecnologías emergentes, sistemas y procesos que se utilizan para hacer la transición del sector manufacturero actual a uno que genere mayor valor a lo largo de la cadena de producción, de acuerdo con el Ministerio de comercio, industria y turismo."),_c('br'),_c('br'),_vm._v("Desde la creación del sistema nacional de competitividad, ciencia, tecnología e innovación, el país ha tenido avances en términos de competitividad a nivel global, la industria textil ha sido, es y seguirá siendo uno de los pilares de la economía manufacturera del país y un gran aliado comercial. Es un sector que se ha transformado en los últimos años porque hay un boom de la manufactura, del diseño y uno del retail. "),_c('br'),_c('br'),_vm._v("La apuesta de una de las estrategias del gobierno con el programa de transformación productiva (PTP) en el sector de la moda y las confecciones con su Visión: Colombia será reconocida internacionalmente como un país innovador en productos y procesos con un talento humano altamente calificado y competitivo a nivel internacional, que le permita añadir valor agregado al sistema moda, convertirá al país en un polo de diseño en el mundo, transformándose en un proveedor de productos de alto valor agregado, especializados y con marca propia. Dicha visión es soportada por el plan de negocio para el sector desarrollado por la empresa de consultoría McKinsey & Company, denominado Informe final Sector Textil, confección, diseño y Moda, en el cual se establece que el sector textil, confección, diseño y moda podrá generar al menos US$14.3 mil millones de ingresos para el 2032 (4 veces los ingresos del 2007), comprometido con un programa sectorial a largo plazo permitiendo consolidar al sector, ya que este es uno de los principales exportadores y generadores de empleo en el país."),_c('br'),_c('br'),_vm._v("Actualmente las empresas de este sector se ubican geográficamente en casi todos los departamentos del país, pero se concentran en especial en la Región Bogotá - Cundinamarca con un 40% de la industria textil y 35% de la industria confección, mientras que la región Antioquia concentra el 53% de la textil y el 30% de la industria de confeccion."),_c('br'),_c('br'),_vm._v("De acuerdo con SUPERSOCIEDADES, la composición empresarial del sector cuenta con alrededor de 30.000 unidades productivas, siendo en mayoría microempresas con un 91%, le siguen las pequeñas entre 2-6%, las medianas entre 1-2% y por último menos del 1% son grandes empresas. Sin embargo, al revisar las cifras a corte de 2016 de la Superintendencia de Sociedades, se encuentra que tan sólo 725 empresas de todo el sector textil-confección, el 24,0% corresponde a grandes empresas, el 56,1% a medianas, el 19,9% a pequeñas y microempresas. "),_c('br'),_c('br'),_vm._v("El informe de SUPERSOCIEDADES refleja la información del subsector de confecciones de prendas incrementó sus ingresos y sus ganancias de 2015 a 2016. Así mismo, presentó aumento en el activo, pasivo y patrimonio."),_c('br'),_c('br'),_vm._v("El programa Tecnología en Supervisión en Procesos de Confección se crea para brindar al sector productivo de confecciones, la posibilidad de formar personal en supervisión de la fabricación de prendas de vestir, con altas calidades laborales, por medio de competencias bajo un esquema de formación teórico-práctica que contribuyan a fortalecer los procesos productivos de las empresas del sector, al desarrollo económico, social y tecnológico de su entorno y del país. "),_c('br'),_c('br'),_vm._v("El SENA es la única entidad que realiza formación especializada en el diseño y desarrollo de prendas de vestir, con presencia en los departamentos de Atlántico, Tolima, Distrito Capital, Antioquia, Boyacá, Cauca, Santanderes entre otras, contribuyendo al desarrollo de la industria textil-confección en estas regiones, al aportar el talento humano que genere valor a su oferta de productos y servicios."),_c('br'),_c('br'),_vm._v("Este programa de formación hace parte de la respuesta a los desafíos propuestos en el Plan de Negocios del sector Textil Confección Diseño y Moda, en los cuales se ve la necesidad del sector de contar con talento humano altamente especializado. Atendiendo esta necesidad, el SENA y la Cámara Colombiana de las Confecciones y Afines han firmado desde 2013 el convenio especial de cooperación 00263, el cual tiene como objeto “Fortalecer la base para la gestión de las empresas, pequeños y medianas del sector de las confecciones, para desarrollar capacidades inherentes al proceso administrativo, el desarrollo de mejores prácticas y habilidades en el análisis y gestión de los procesos, con el propósito de establecer escenarios de mejora en la productividad de las diferentes áreas de la organización y consolidar el sector ante los nuevos retos de los mercados, según acuerdos comerciales y las nuevas condiciones del mercado de la confección y afines”. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro p-4 p-md-5 mb-5"},[_c('div',{staticClass:"titulo__template--a mb-4"},[_c('span',{staticClass:"h4"},[_vm._v("3. Competencias a desarrollar")])]),_c('ul',{staticClass:"lista-ul mb-4"},[_c('li',{staticClass:"d-flex"},[_c('i',{staticClass:"fas fa-angle-right"}),_vm._v("Aplicación de conocimientos de las ciencias naturales de acuerdo con situaciones del contexto productivo y social.")]),_c('li',{staticClass:"d-flex"},[_c('i',{staticClass:"fas fa-angle-right"}),_vm._v("Aplicar prácticas de protección ambiental, seguridad y salud en el trabajo de acuerdo con las políticas organizacionales y la normatividad vigente.")]),_c('li',{staticClass:"d-flex"},[_c('i',{staticClass:"fas fa-angle-right"}),_vm._v("Controlar la calidad en los procesos de tejeduría de acuerdo con las normas establecidas.")]),_c('li',{staticClass:"d-flex"},[_c('i',{staticClass:"fas fa-angle-right"}),_vm._v("Controlar la producción de acuerdo con métodos técnicos y normativa.")]),_c('li',{staticClass:"d-flex"},[_c('i',{staticClass:"fas fa-angle-right"}),_vm._v("Desarrollar procesos de comunicación oral y escritos en forma eficaz